// Card
.card {
  display: flex;
  align-items: start;
  position: relative;
  padding: 1em;

  overflow: hidden;
  flex-direction: column;
  color: $color-background;
  border-radius: 8px;
  background-color: scale-color($color-foreground, $lightness: +5%);
}

.card h3 {
  line-height: 1.3;
}

.card a {
  color: $color-background;
}

.card .outgoing:hover {
  &:after {
    display: inline-block;
    margin-left: 5px;

    width: .8em;
    height: .8em;
    content: '';
    background: url(/img/external-link.svg) no-repeat;
    background-size: .8em .8em;
  }
}

.card article {
  margin-top: .5em;

  font-size: $size-400;
  line-height: 1.45;
}

.card .tag {
  position: absolute;
  top: 0;
  right: 0;
  padding: 3px 8px;

  font-size: $size-300;
  font-weight: 600;
  color: $color-background;
  border-bottom-left-radius: 8px;
  background-color: $color-accent;
}

.card .learn-more {
  display: flex;
  align-items: center;
  margin-top: .5em;

  font-size: $size-400;
  font-weight: 600;

  &:after {
    margin-left: 5px;

    content: '•••';
  }
}

// Navigation
nav {
  display: flex;
  margin-top: 1em;

  flex-wrap: wrap;
  width: calc(100% + 10px);
  transform: translateX(-5px);
}

nav .tag {
  padding: 5px 8px;

  font-size: $size-300;
  font-weight: 600;
  color: $color-background;
  border-radius: 4px;
  background-color: $color-accent;
}

nav .tag {
  margin: 5px;
}

// Carbon Ads

.carbon-box {
  margin-top: 2em;

  height: 100px;
}

#carbonads * {
  margin: initial;
  padding: initial;
}

#carbonads {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
  Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', Helvetica, Arial,
  sans-serif;
}

#carbonads {
  display: flex;

  max-width: 360px;
  background-color: hsl(0, 0%, 10%);
  box-shadow: 0 0 1px hsla(0, 0%, 0%, .5);
}

#carbonads a {
  text-decoration: none;
  color: #ddd;
}

#carbonads a:hover {
  color: #ddd;
}

#carbonads span {
  display: block;
  position: relative;

  overflow: hidden;
}

#carbonads .carbon-wrap {
  display: flex;
}

#carbonads .carbon-img {
  display: block;
  margin: 0;

  line-height: 1;
}

#carbonads .carbon-img img {
  display: block;
}

#carbonads .carbon-text {
  margin-bottom: 16px;
  padding: 10px;

  font-size: 13px;
  line-height: 1.5;
  text-align: left;
}

#carbonads .carbon-poweredby {
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 6px 8px;

  font-size: 8px;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  letter-spacing: .5px;
  text-transform: uppercase;
  color: #aaa;
  border-top-left-radius: 6px;
  background: #1e2021;
}
