.container {
  display: grid;
  gap: 20px 10px;
  padding: 10px;
  grid-template-columns: repeat(8, 1fr);
  margin: 0 auto;
  max-width: 1280px;
}

body {
  padding: 5em 1em;
}

header h1 {
  line-height: 1;
}

header p {
  font-size: $size-500;
  margin-top: .5em;
}

footer {
  margin-top: 2em;
}

header,
nav,
main,
footer {
  grid-column: 1/-1;
}

main {
  margin-top: 2em;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px 16px;
}

@media only screen and (min-width: 320px) and (max-width: 759px) {
  html {
    font-size: 14px;
  }
  main {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px 16px;
  }
}