html {
  font-size: 16px;
}

body {
  font-family: $font-main;
  line-height: 1.5;
  color: $color-foreground;
  background-color: $color-background;
}

img {
  display: block;

  max-width: 100%;
}

li {
  transition: background-color 0.18s ease-in-out, color 0.18s ease-in-out,
    border-color 0.18s ease-in-out;
}

a {
  text-decoration: none;
  color: $color-accent;
}

a:not([class]) {
  color: $color-accent;
}

::selection {
  color: $color-accent;
  background-color: $color-background;
}

[href]:hover {
  text-decoration: none;
}

ul,
ol {
  margin: 0;
  padding: 0;

  list-style: none;
}

h1 {
  max-width: 25ch;
}

h1,
h2 {
  font-weight: 700;
  line-height: 1.35;
}

h1 {
  font-size: $size-700;
}

h2 {
  font-size: $size-600;
}

h3 {
  font-size: $size-500;
}

.flow > * + * {
  margin-top: 1em;
}