// Typography
$size-200: 0.625rem;
$size-300: 0.75rem;
$size-400: 1rem;
$size-500: 1.33rem;
$size-600: 1.77rem;
$size-700: 2.36rem;
$size-800: 3.15rem;
$size-900: 4.2rem;

$flow-space: 1em;

// Base Variable
$font-main: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans,
  Ubuntu, Cantarell, 'Helvetica Neue', Helvetica, Arial, sans-serif;
$font-mono: 'Input Mono', 'Source Code Pro', Menlo, Monaco, 'Courier New',
  monospace, serif;
$font-serif: Georgia, serif;
$color-accent: #efcc68;
$color-background: #000411;
$color-foreground: #e2efe6;
$color-middleground: #aeb7b3;